<template>
    <div>
        <button @click="formatData()"></button>
        <!-- excel -->
        <div ref="spreadsheetContainer" style="width: 400px; height: 375px;"></div>
        <!-- echart -->
        <div ref="chartRef" style="width: 600px; height: 400px;"></div>
    </div>
</template>

<script setup>
    import { ref, onMounted, onUnmounted } from 'vue';
    import Spreadsheet from 'x-data-spreadsheet';
    // 
    import 'x-data-spreadsheet/dist/locale/zh-cn';
    Spreadsheet.locale('zh-cn');

    import * as echarts from 'echarts';

    const spreadsheetContainer = ref(null);
    let spreadsheetInstance = null;
    let _ci = -1
    let _ri = -1

    const chartRef = ref(null);
    let myChart = null;
    onMounted(() => {
        // excel
        spreadsheetInstance = new Spreadsheet(spreadsheetContainer.value, {
            // 
            // mode: 'edit', // 'edit'  'read'
            showToolbar: false, // 
            showBottomBar: false,   // 
            // showGrid: true, // 
            // showContextmenu: true, // false

            // 
            view: {
                height: () => 375, // document.documentElement.clientHeight
                width: () => 400 // document.documentElement.clientWidth
            },

            // 
            row: {
                len: 10,          // 
                height: 30,       // 
                // 0: { height: 50 } // 
                // indexWidth: 60, // 
                // minWidth: 60, // 
            },
            col: {
                len: 3,          // 
                width: 100,       // 
                // 0: { width: 200 } // 
            },
            // 
            // style: {
            //     // 
            //     bgcolor: '#ffffff',
            //     // 
            //     align: 'left',
            //     // 
            //     valign: 'middle',
            //     // 
            //     textwrap: false,
            //     // 
            //     strike: false,
            //     // 
            //     underline: false,
            //     // 
            //     color: '#0a0a0a',
            //     // 
            //     font: {
            //         // 
            //         name: 'Helvetica',
            //         // 
            //         size: 10,
            //         // 
            //         bold: false,
            //         // 
            //         italic: false,
            //     },
            // }
            // 
            // styles: [
            //     { bgcolor: '#ffff00', color: '#ff0000', font: { size: 14, bold: true }, align: 'center' },
            //     { bgcolor: '#ccffcc', textwrap: true, border: { bottom: ['thin', '#000000'] } },
            // ]
        });
        // 
        spreadsheetInstance.loadData({
            rows: {
                0: { 
                    cells: { 
                        0: { text: 'Mon' },
                        1: { text: '100' },
                        2: { text: '80' },
                    } 
                },
                1: { 
                    cells: { 
                        0: { text: 'Tue' },
                        1: { text: '200' },
                        2: { text: '160' },
                    } 
                },
                2: { 
                    cells: { 
                        0: { text: 'Wed' },
                        1: { text: '300' },
                        2: { text: '240' },
                    } 
                },
                3: { 
                    cells: { 
                        0: { text: 'Thu' },
                        1: { text: '400' },
                        2: { text: '320' },
                    } 
                },
                4: { 
                    cells: { 
                        0: { text: 'Fri' },
                        1: { text: '500' },
                        2: { text: '400' },
                    } 
                },
                5: { 
                    cells: { 
                        0: { text: 'Sat' },
                        1: { text: '600' },
                        2: { text: '480' },
                    } 
                },
                6: { 
                    cells: { 
                        0: { text: 'Sun' },
                        1: { text: '700' },
                        2: { text: '560' },
                    } 
                }
            },
            // styles: [ //  styles
            //     { bgcolor: '#ffff00', color: '#ff0000', font: { size: 14, bold: true } },
            // ],
        });

        // 
        // spreadsheetInstance.cellText(0, 0, '');

        // 
        /**
         * cell-selected
         * cells-selected
         * cell-edited
         * change 
         */
        // 
        spreadsheetInstance.on('cell-selected', (cell, ri, ci) => {
            // 
            _ci = ci
            _ri = ri
            console.log(`:  ${ri},  ${ci}`);
        });
        // spreadsheetInstance.on('cells-selected', (cell, { sri, sci, eri, eci }) => {});
        spreadsheetInstance.on('cell-edited', (text, ri, ci) => {
            let _row = spreadsheetInstance.getData()[0].rows.len
            if (ri == _row - 1) {
                let type = 'row', n = 1;
                insert(type, _ci, _ri, n)
            }
        });

        // echart
        if (chartRef.value) {
            myChart = echarts.init(chartRef.value);
            const option = {
                // title: {
                //   // 
                //   text: '',
                //   // 
                //   subtext: '',
                //   left: 'center' // 
                // },
                tooltip: {},
                xAxis: {
                    // data: [ "","","","","","" ]
                },
                yAxis: {
                    type: 'value'
                },
                series: [
                    // {
                    //     name: '',
                    //     type: 'line',
                    //     data: [5, 20, 36, 10, 10, 20]
                    // }
                ]
            };
            myChart.setOption(option);
        }

        document.addEventListener('keydown', (e) => {
            if (e.key === 'Tab') {
                // 
                let _col = spreadsheetInstance.getData()[0].cols.len
                // let _row = spreadsheetInstance.getData()[0].rows.len
                // 
                if (_ci === _col - 1) {
                    e.preventDefault();
                    let type = 'column', n = 1;
                    insert(type, _ci, _ri, n)
                }
            }
        })
    });
    onUnmounted(() => {
        spreadsheetInstance.value = null; // 

        if (myChart !== null && myChart !== undefined) {
            myChart.dispose(); // 
        }
    });
    // 
    function insert(type, _ci, _ri, n = 1) {
        let { datas } = spreadsheetInstance

        datas[0].changeData(() => {
            // const { sri, sci } = this.selector.range;
            // let si = sri;
            const { rows, merges, cols } = datas[0];
            let si = _ri
            if (type === 'row') {
                rows.insert(_ri + 1, n);
            } else if (type === 'column') {
                rows.insertColumn(_ci + 1, n);
                si = _ci
                cols.len += n;
                Object.keys(cols._).reverse().forEach((colIndex) => {
                const col = parseInt(colIndex, 10);
                if (col >= sci) {
                    cols._[col + n] = cols._[col];
                    delete cols._[col];
                }
                });
            }
            merges.shift(type, si, n, (ri, ci, rn, cn) => {
                const cell = rows.getCell(ri, ci);
                cell.merge[0] += rn;
                cell.merge[1] += cn;
            });
        });
        // 
        const data = spreadsheetInstance.getData()
        spreadsheetInstance.sheet.loadData(data)
    }

    // 
    function formatData() {
        // x-spreadsheet
        let data = spreadsheetInstance.getData();

        let chart_data = []
        let series = []
        for (let i in data[0].rows) {
            if (i !== 'len') {
                let cells = data[0].rows[i].cells
                for (let j in cells) {
                    if (j === '0') chart_data.push(cells[j].text)
                    else if (!series[+j - 1]) {
                        series.push({
                            data: [cells[+j].text],
                            type: 'line'
                        })
                    } else {
                        series[+j - 1].data.push(cells[+j].text)
                    }
                }
            }
        }
        console.log(chart_data)
        console.log(series)

        let option = {
            // 
            xAxis: {
                type: 'category',
                data: chart_data
            },
            series
        };

        myChart.setOption(option)
    }
</script>
  